// src/features/drone-early-inform/components/DroneEarlyInformSettings.jsx
"use client"

import * as React from "react"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"

const API_BASE = "/api/drone-early-inform"

function createFormState(lineId) {
  return {
    lineId: lineId ?? "",
    mainStep: "",
    customEndStep: "",
  }
}

function toDraft(row) {
  return {
    lineId: String(row?.line_id ?? ""),
    mainStep: String(row?.main_step ?? ""),
    customEndStep: row?.custom_end_step === null || row?.custom_end_step === undefined
      ? ""
      : String(row.custom_end_step),
  }
}

function sanitizeDraft(row, draft) {
  const base = toDraft(row)
  return {
    lineId: (draft?.lineId ?? base.lineId).trim(),
    mainStep: (draft?.mainStep ?? base.mainStep).trim(),
    customEndStep: (draft?.customEndStep ?? base.customEndStep).trim(),
  }
}

function hasDraftChanges(row, draft) {
  if (!row) return false
  const next = sanitizeDraft(row, draft)
  const base = toDraft(row)
  return (
    next.lineId !== base.lineId.trim() ||
    next.mainStep !== base.mainStep.trim() ||
    next.customEndStep !== base.customEndStep.trim()
  )
}

export function DroneEarlyInformSettings({ lineId }) {
  const [rows, setRows] = React.useState([])
  const [loading, setLoading] = React.useState(false)
  const [error, setError] = React.useState(null)

  const [drafts, setDrafts] = React.useState({})
  const [rowStatus, setRowStatus] = React.useState({})

  const [formState, setFormState] = React.useState(() => createFormState(lineId))
  const [formError, setFormError] = React.useState(null)
  const [isSubmitting, setIsSubmitting] = React.useState(false)

  const resolvedLineId = React.useMemo(() => {
    if (typeof lineId !== "string") return ""
    return lineId.trim()
  }, [lineId])

  React.useEffect(() => {
    setFormState((prev) => ({
      ...prev,
      lineId: resolvedLineId || prev.lineId,
    }))
  }, [resolvedLineId])

  const fetchRows = React.useCallback(async () => {
    setLoading(true)
    setError(null)
    try {
      const query = resolvedLineId ? `?lineId=${encodeURIComponent(resolvedLineId)}` : ""
      const response = await fetch(`${API_BASE}${query}`, {
        cache: "no-store",
      })

      if (!response.ok) {
        const payload = await response.json().catch(() => null)
        throw new Error(payload?.error ?? "Failed to load records")
      }

      const payload = await response.json()
      const nextRows = Array.isArray(payload?.rows) ? payload.rows : []
      setRows(nextRows)
      setDrafts({})
      setRowStatus({})
    } catch (fetchError) {
      setError(fetchError?.message ?? "Failed to load records")
    } finally {
      setLoading(false)
    }
  }, [resolvedLineId])

  React.useEffect(() => {
    fetchRows()
  }, [fetchRows])

  const handleFormChange = React.useCallback((event) => {
    const { name, value } = event.target
    setFormState((prev) => ({
      ...prev,
      [name]: value,
    }))
  }, [])

  const handleCreate = React.useCallback(async (event) => {
    event.preventDefault()
    setFormError(null)

    const nextLineId = (formState.lineId || resolvedLineId || "").trim()
    const nextMainStep = (formState.mainStep || "").trim()
    const nextCustomEndStep = (formState.customEndStep || "").trim()

    if (!nextLineId) {
      setFormError("라인 ID를 입력해주세요.")
      return
    }
    if (!nextMainStep) {
      setFormError("메인 공정을 입력해주세요.")
      return
    }

    setIsSubmitting(true)
    try {
      const response = await fetch(API_BASE, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          lineId: nextLineId,
          mainStep: nextMainStep,
          customEndStep: nextCustomEndStep || null,
        }),
      })

      const payload = await response.json().catch(() => null)
      if (!response.ok) {
        throw new Error(payload?.error ?? "저장에 실패했습니다.")
      }

      await fetchRows()
      setFormState(createFormState(nextLineId))
    } catch (submitError) {
      setFormError(submitError?.message ?? "저장에 실패했습니다.")
    } finally {
      setIsSubmitting(false)
    }
  }, [fetchRows, formState.customEndStep, formState.lineId, formState.mainStep, resolvedLineId])

  const handleDraftChange = React.useCallback((id, field, value) => {
    setDrafts((prev) => ({
      ...prev,
      [id]: {
        ...prev[id],
        [field]: value,
      },
    }))
  }, [])

  const handleResetRow = React.useCallback((id) => {
    setDrafts((prev) => {
      const next = { ...prev }
      delete next[id]
      return next
    })
    setRowStatus((prev) => {
      const next = { ...prev }
      if (next[id]) {
        next[id] = { ...next[id], error: null }
      }
      return next
    })
  }, [])

  const handleSaveRow = React.useCallback(async (row) => {
    if (!row) return
    const draft = drafts[row.id]
    const nextValues = sanitizeDraft(row, draft)

    if (!hasDraftChanges(row, draft)) {
      return
    }

    setRowStatus((prev) => ({
      ...prev,
      [row.id]: { saving: true, deleting: false, error: null },
    }))

    try {
      const response = await fetch(`${API_BASE}/${row.id}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          lineId: nextValues.lineId,
          mainStep: nextValues.mainStep,
          customEndStep: nextValues.customEndStep || null,
        }),
      })

      const payload = await response.json().catch(() => null)
      if (!response.ok) {
        throw new Error(payload?.error ?? "수정에 실패했습니다.")
      }

      const updatedRow = payload?.row ?? row
      setRows((prev) =>
        prev.map((current) => (current.id === row.id ? updatedRow : current))
      )
      handleResetRow(row.id)
    } catch (updateError) {
      setRowStatus((prev) => ({
        ...prev,
        [row.id]: {
          saving: false,
          deleting: false,
          error: updateError?.message ?? "수정에 실패했습니다.",
        },
      }))
      return
    }

    setRowStatus((prev) => ({
      ...prev,
      [row.id]: { saving: false, deleting: false, error: null },
    }))
  }, [drafts, handleResetRow])

  const handleDeleteRow = React.useCallback(async (row) => {
    if (!row) return
    if (!window.confirm(`정말로 ${row.main_step} 항목을 삭제하시겠습니까?`)) {
      return
    }

    setRowStatus((prev) => ({
      ...prev,
      [row.id]: { saving: false, deleting: true, error: null },
    }))

    try {
      const response = await fetch(`${API_BASE}/${row.id}`, { method: "DELETE" })
      const payload = await response.json().catch(() => null)
      if (!response.ok) {
        throw new Error(payload?.error ?? "삭제에 실패했습니다.")
      }

      setRows((prev) => prev.filter((current) => current.id !== row.id))
      setDrafts((prev) => {
        const next = { ...prev }
        delete next[row.id]
        return next
      })
      setRowStatus((prev) => {
        const next = { ...prev }
        delete next[row.id]
        return next
      })
    } catch (deleteError) {
      setRowStatus((prev) => ({
        ...prev,
        [row.id]: {
          saving: false,
          deleting: false,
          error: deleteError?.message ?? "삭제에 실패했습니다.",
        },
      }))
    }
  }, [])

  return (
    <section className="grid gap-6">
      <div className="rounded-xl border bg-card p-6 shadow-sm">
        <div className="flex flex-col gap-2">
          <div className="flex flex-wrap items-center justify-between gap-3">
            <div>
              <h1 className="text-2xl font-semibold tracking-tight">
                Drone Early Inform · {resolvedLineId || "전체"}
              </h1>
              <p className="mt-1 text-sm text-muted-foreground">
                drone_early_inform_v3 테이블에 등록된 공정 매핑을 조회하고 편집합니다.
              </p>
            </div>
            <Button onClick={fetchRows} disabled={loading} variant="outline">
              새로고침
            </Button>
          </div>
          {error ? (
            <p className="text-sm text-destructive">{error}</p>
          ) : null}
        </div>
      </div>

      <div className="rounded-xl border bg-card p-6 shadow-sm">
        <h2 className="text-lg font-semibold">새 매핑 추가</h2>
        <p className="mt-1 text-sm text-muted-foreground">
          라인 ID와 메인 공정은 필수입니다. 커스텀 종료 공정은 비워두면 미설정으로 저장됩니다.
        </p>
        <form onSubmit={handleCreate} className="mt-4 grid gap-4">
          <div className="grid gap-2 md:grid-cols-3 md:items-center md:gap-4">
            <label className="text-sm font-medium" htmlFor="lineId">
              라인 ID
            </label>
            <Input
              id="lineId"
              name="lineId"
              value={formState.lineId}
              onChange={handleFormChange}
              placeholder="예: LINE_A"
            />
          </div>
          <div className="grid gap-2 md:grid-cols-3 md:items-center md:gap-4">
            <label className="text-sm font-medium" htmlFor="mainStep">
              메인 공정
            </label>
            <Input
              id="mainStep"
              name="mainStep"
              value={formState.mainStep}
              onChange={handleFormChange}
              placeholder="예: SMT"
            />
          </div>
          <div className="grid gap-2 md:grid-cols-3 md:items-center md:gap-4">
            <label className="text-sm font-medium" htmlFor="customEndStep">
              커스텀 종료 공정 (선택)
            </label>
            <Input
              id="customEndStep"
              name="customEndStep"
              value={formState.customEndStep}
              onChange={handleFormChange}
              placeholder="예: 검사 완료"
            />
          </div>
          {formError ? (
            <p className="text-sm text-destructive">{formError}</p>
          ) : null}
          <div className="flex justify-end">
            <Button type="submit" disabled={isSubmitting}>
              {isSubmitting ? "저장 중…" : "추가"}
            </Button>
          </div>
        </form>
      </div>

      <div className="rounded-xl border bg-card p-6 shadow-sm">
        <h2 className="text-lg font-semibold">등록된 매핑</h2>
        <p className="mt-1 text-sm text-muted-foreground">
          각 행에서 값을 수정한 뒤 저장을 눌러 반영할 수 있습니다.
        </p>
        <div className="mt-4">
          {loading ? (
            <p className="text-sm text-muted-foreground">불러오는 중…</p>
          ) : rows.length === 0 ? (
            <p className="text-sm text-muted-foreground">등록된 매핑이 없습니다.</p>
          ) : (
            <TableContainer className="rounded-lg border">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead className="w-24">ID</TableHead>
                    <TableHead>라인 ID</TableHead>
                    <TableHead>메인 공정</TableHead>
                    <TableHead>커스텀 종료 공정</TableHead>
                    <TableHead className="w-[180px] text-right">작업</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {rows.map((row) => {
                    const draft = drafts[row.id]
                    const status = rowStatus[row.id] || { saving: false, deleting: false, error: null }
                    const values = sanitizeDraft(row, draft)
                    const dirty = hasDraftChanges(row, draft)

                    return (
                      <TableRow key={row.id}>
                        <TableCell className="font-mono text-xs text-muted-foreground">
                          {row.id}
                        </TableCell>
                        <TableCell>
                          <Input
                            value={values.lineId}
                            onChange={(event) =>
                              handleDraftChange(row.id, "lineId", event.target.value)
                            }
                          />
                        </TableCell>
                        <TableCell>
                          <Input
                            value={values.mainStep}
                            onChange={(event) =>
                              handleDraftChange(row.id, "mainStep", event.target.value)
                            }
                          />
                        </TableCell>
                        <TableCell>
                          <Input
                            value={values.customEndStep}
                            onChange={(event) =>
                              handleDraftChange(row.id, "customEndStep", event.target.value)
                            }
                            placeholder="(미설정)"
                          />
                        </TableCell>
                        <TableCell>
                          <div className="flex flex-wrap justify-end gap-2">
                            <Button
                              variant="outline"
                              size="sm"
                              disabled={!dirty || status.saving || status.deleting}
                              onClick={() => handleSaveRow(row)}
                            >
                              {status.saving ? "저장 중…" : "저장"}
                            </Button>
                            <Button
                              variant="ghost"
                              size="sm"
                              disabled={!dirty || status.saving || status.deleting}
                              onClick={() => handleResetRow(row.id)}
                            >
                              초기화
                            </Button>
                            <Button
                              variant="destructive"
                              size="sm"
                              disabled={status.saving || status.deleting}
                              onClick={() => handleDeleteRow(row)}
                            >
                              {status.deleting ? "삭제 중…" : "삭제"}
                            </Button>
                          </div>
                          {status.error ? (
                            <p className="mt-2 text-xs text-destructive">{status.error}</p>
                          ) : null}
                        </TableCell>
                      </TableRow>
                    )
                  })}
                </TableBody>
              </Table>
            </TableContainer>
          )}
        </div>
      </div>
    </section>
  )
}
